import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

import 'rxjs/add/operator/map';

@Injectable()
export class SessionProvider {

  constructor(public http: Http, private transfer: FileTransfer, private file: File) {
    console.log('Hello SessionProvider Provider');
  }

  fileTransfer: FileTransferObject = this.transfer.create();

  upload(filePath: string) {
    let options: FileUploadOptions = {
       fileKey: 'sessionFile',
       fileName: Date.now()+'',
       mimeType: 'image/jpeg',
       headers: {Authorization: localStorage.getItem('id_token')}
    }

    return this.fileTransfer.upload(filePath, '/session/create', options, true);
  }

  createSession(session) {
    // return this.http.post()
  }

  listSession() {
    let headers = new Headers();
    headers.append('Authorization', localStorage.getItem('id_token'));
    return this.http.get('/session/list', {headers: headers}).map(res => res.json());
  }

  viewSession(sessionId) {
    this.http.get('/session/view').map(res => res.json())
        .subscribe( data => {
          if(data.success) {
            let session = data.data;
            for(let file in session) {
              if(session[file].type === 'video'){
                //
              }
            }
          }
        });
  }

}



